//Compatibility with Calendar plugin
.js-calendar.fc.fc-unthemed.fc-ltr-rendered {
    padding-top: 130px;

    table {
        margin-bottom: 0;
    }   
}
#modal-content {
    .js-calendar.fc.fc-unthemed.fc-ltr-rendered {
        padding: 0;
        .fc-toolbar.fc-header-toolbar {
            position: sticky;
            top: 31px;
            z-index: 10;
            background-color: $default-bg-color;
            padding: 0 15px 15px 15px;
        }

        .fc-view {
            padding: 0 15px 15px 15px;

            table {
                margin-bottom: 0;
            }   
        }
    }
}


//Compatibility with Gantt plugin
.page #main .menu-inline {
    padding-top: 130px;
  }
  
  #gantt-chart {
    margin-bottom: 10px;
  
    .ganttview-vtheader-series-name:last-child {
      border-bottom: 1px solid #999;
    }
}

//Compatibility with Wiki plugin
.clearfix {
    padding-top: 140px;

    .column.content {
        a {
            i {
                margin-right: 5px;
                margin-bottom: 10px;
            }
        }
    }
}
.table-list-row {
    h1 {
        padding: 5px;
    }
}

//Compatibility with task2pdf plugin
//Compatibility with groupassign plugin
//Compatibility with markdownplus plugin
//Compatibility with calendar plugin
input:not(.select2-search__field):not([type="checkbox"]):not([name="notifications_filter"]):not([name="positionChoice"]),
input[type="number"],
input[type="date"],
input[type="email"],
input[type="password"],
input[type="text"]:not(.input-addon-field),
textarea,
select {
  &[name="task2pdf_cjk"],
  &[name="enable_am_group_management"],
  &[name="unicode_shortcode"],
  &[name="calendar_project_tasks"],
  &[name="calendar_user_tasks"] {
    width: auto;
    margin: 0 10px 0 0;

    &:focus,
    &:active {
      box-shadow: none;
    }
  }
}