.alert {
  color: white;
  border: 0 none;
  border-radius: 5px;
  background-color: $alert-color;
  margin-top: 0;
  margin-bottom: 1rem;

  &.alert-success {
    background-color: $alert-color-success;
  }

  &.alert-error {
    background-color: $alert-color-error;
  }

  &.alert-info {
    background-color: $alert-color-info;
  }

  &.alert-fade-out {
    width: 100%;
    margin-bottom: 0;
    left: 0;
    border-radius: 0;
  }
}