#modal-box {

  .alert {
    margin: 15px;
    border-radius: 5px;
  }

  .js-confirm-buttons-rendered {
    padding: 15px;
    border-top: 1px solid $border-color;
    display: flex;

    .form-actions {
      padding: 0;
      margin-left: auto;
    }
  }

  #modal-close-button:hover,
  #modal-close-button:focus {
    color: black;
  }
  
  @media (min-width: 1350px) {
    width: 1140px !important;
  }

  #modal-header {
    position: sticky;
    padding-top: 12px;
    top: 0;
    background-color: $default-bg-color;
    padding-right: 15px;
    z-index: 10;

  }

  #modal-content {
    padding: 0;
    top: 11px;

    .page-header {

      ul {
        margin: 0 0 -14px -15px;
        width: 1140px;
        border-bottom: 0 none;
        border-top: 1px solid $border-color;

      }
    }
    form {
      padding: 15px;

      #form-title {
        width: 97%;
        margin-right: 10px;
        display: inline-block;
        
      }
      .form-errors li {
        padding-bottom: 15px;

      }
      .form-required {
        display: inline-block;

        &:after {
          display: none;
        }
      }

      select[name="owner_id"] {
        margin-bottom: .4rem;

      }

      select[id="form-owner_id"] {
        width: 80%;
        display: inline-block;

      }

      select[id="form-user_id"] {
        width: 95%;
        display: inline-block;
        margin-right: 5px;

        & ~ small {
          display: contents;
          
          a {
            text-decoration: none;
      
            &:hover,
            &:focus {
            text-decoration: none;
            }
          }
        }
      }

      small {
        float: right;
        line-height: 35px;
        vertical-align: middle;
      }

      .assign-me {
        display: inline-block;
        margin-bottom: 1rem;
      }

      .form-numeric {
        display: inline;
        width: 40%;
        margin-right: 8px;
      }

      input#form-name,
      input#form-label,
      input#form-username,
      input#form-text,
      input#form-filter,
      input#form-role,
      input#form-emails,
      input#form-subject {
        width: 97%;
        display: inline;
        margin-right: 10px;

        ~ input#form-name {
          width: 100%;
          margin-right: 0;
        }
      }

      &[id="project-creation-form"], &[action="/?controller=GroupCreationController&action=save"] {
        #form-name {
          width: 98%;
          display: inline;
          margin-right: 10px;
        }
      }

      &[action^="/?controller=TaskDuplicationController&action=move"],
      &[action^="/?controller=TaskDuplicationController&action=copy"] {
        .select-dropdown-input-container {
          border: 1px solid $border-color;
          margin-bottom: 1rem;

          input.select-dropdown-input::placeholder {
            color: $default-title-color;
          }
        }
      }

      &[action^="/?controller=TaskMailController&action=send"] {
        input {
          width: 98%;
          display: inline;
          margin-right: 10px;
        }
      }

      .js-project-creation-options {
        margin-top: 0;
        padding-bottom: 0;
        margin-bottom: 10px;
        padding-top: 0;
        border-left: 0 none;
        padding-left: 0;

        .alert {
          margin-bottom: 10px;
          margin-left: 0;
        }
      }

      &[action^="/?controller=TaskDuplicationController&action="] {
        select[id="form-owner_id"] {
          width: 100%;
        }
      }
    }

    .page-header {
      padding: 15px;
      background-color: $default-bg-color;
      border-bottom: 1px solid $border-color;
      position: sticky;
      top: 0;
      z-index: 1;
      margin: 0;
      margin-top: -31px;

      h2 {
        border-bottom: 0;
      }
    }

    .sidebar-container {
      margin-top: 20px;

      .sidebar {
        max-height: 350px;
        top: 120px;

        li {
          margin-left: 15px;
        }
      }
    }
  }

  .js-file-upload-rendered ~ .js-submit-buttons-rendered {
    padding: 15px;
    margin: 0;

    .form-actions {
      padding: 0;
      text-align: right;
    }
  }

  .js-file-upload-rendered {
    .alert ~ button {
      margin-left: 15px;
    }
  }
  
  .js-submit-buttons-rendered {
    padding: 15px;
    margin: 0 -15px -15px -15px;

    .form-actions {
      padding: 0;
      text-align: right;
    }
  }
}
