.avatar {

  &.avatar-inline {
    margin-right: 8px;
  }

  &.avatar-20 {

    .avatar-letter {
      width: 30px;
      line-height: 30px;
    }
  }

  &.avatar-48 {

    .avatar-letter {
      font-size: 18px;
    }
  }

  img,
  div {
    border-radius: 50%;
    line-height: 38px;
    width: 38px;
  }
}

#user-section .avatar.avatar-48 img[src^="/?controller=AvatarFileController&action=image"] {
  margin-left: 5rem;
}