#tooltip-container {
  z-index: 10;
  box-shadow: 0px 15px 15px 5px rgba(187, 218, 247, .25);
  border: 0 none;

  &:before {
    content: '';
    height: 17px;
    width: 100%;
    position: absolute;
    top: -17px;
    left: 0;
  }
  
  table {
    margin-bottom: 0;
  }
}

header {
  background-color: $default-bg-color-85;
  padding: 0 15px;
  border-bottom: 0;
  margin-bottom: 0;
  color: white;
  height: 60px;
  align-items: center;
  position: fixed;
  width: 100%;
  z-index: 10;

  .title {
    margin-left: 10px;
    padding-left: 10px;
    border-left: 1px solid #fff;
    display: inline-block;
    vertical-align: middle;
  }

  .logo {
    display: inline-block;
    vertical-align: middle;

    a {
      color: white;
      transition: color .3s;
      opacity: 1;

      img {
        height: 38px;
        vertical-align: middle;
      }

      &:hover,
      &:focus {
        color: white;

        span {
          color: white;
        }
      }

      span {
        color: white;
      }
    }
  }

  a {
    text-decoration: none;

    .fa {
      color: white;
    }
  }

  .board-selector-container {
    margin-right: 30px;

    .js-select-dropdown-autocomplete-rendered {
      width: 100%;
    }
  }

  .menus-container {
    height: 100%;

    .header-creation-menu {

      a {

        .fa.fa-plus {
          margin-right: 8px;
        }
      }
    }

    .notification,
    .dropdown {
      height: 100%;
      margin-left: -4.19px;

      a {
        padding: 0 10px;
        height: 100%;
        display: flex;
        align-items: center;
        transition: background-color .3s;

        &:hover,
        &:focus {
          background-color: $default-bg-color-85;
        }
      }
    }

    .dropdown {

      .fa-caret-down {
        color: $default-bg-color-60;
      }
    }

    .dropdown-menu-link-icon {
      display: flex;
      align-items: center;

      .avatar img {
        width: 38px;
      }
    }
  }
}


.project-header,
.page-header {
  padding: 0;
}

.page > #main > .project-header,
.page > #main > .page-header,
.page > .project-header,
.page > .page-header {
  position: fixed;
  z-index: 10;
  width: 100%;
  top: 0;

}

.page > #main > .project-header,
.page > #main > .page-header,
.page > .project-header,
.page > .page-header {
  margin: 60px -15px 15px -15px;

}

.page,
.page > #main {

  > .page-header,
  > .project-header {
    background-color: $default-bg-color-1;
    border-bottom: 1px solid $default-bg-color-5;
  }
}

.page-header {

  h2 {
    border-bottom: 0;
    font-weight: 300;
    padding: 1rem 0;
    color: $default-title-color;

    ~ ul {
      margin: 0;
      background-color: $default-bg-color-1;
      border-bottom: 1px solid $default-bg-color-5;
    }

    a {
      color: $default-title-color;
      
      &:hover,
      &:focus {
        color: black;
        text-decoration: none;
      }
    }
  }

  ul {
    margin-top: 0;
    text-align: initial;
    width: 100%;

    li {
      padding-right: 0;

      a {
        color: initial;
        text-decoration: none;
        font-size: .9rem;
        line-height: 45px;
        display: inline-block;
        padding: 0 15px;
        transition: background-color .3s;

        &:hover,
        &:focus {
          background-color: darken($default-bg-color-1, 3);
        }

        i.fa {
          margin-right: 8px;
          color: $default-bg-color-15;
        }
      }

      &.active {
        a:hover,
        a:focus {
          text-decoration: none;
        }
      }
    }
  }
}

.project-header {
  padding: 15px;
  display: flex;
  align-items: center;

  .dropdown-component {
    margin: 0 15px 0 0;

    i:first-child {
      padding-right: 3px;
    }

    i {
      color: $default-title-color;
    }

    &:hover {
      i {
        color: black;
      }
    }
  }

  .input-addon,
  .filter-box {
    margin-bottom: 0;
  }

  .views-switcher-component {
    margin-top: 0;
    float: none;
  }

  .filter-box-component {
    margin-left: auto;

    .input-addon {
      min-width: 400px;
    }
  }
}
