#board-container {
  margin: 0 -15px -25px -15px;
  overflow-x: inherit;

  table {
    border-collapse: separate;
    border-spacing: 10px 0;

    .board-column-header {
      
      &::before {
        content: '';
        height: 16px;
        background-color: $default-bg-color;
        position: fixed;
        left: 0;
        right: 0;
        top: 129px;
        z-index: 10;
      }
  
      .board-add-icon {
        margin: 0 5px;

        i {
          
          &:before {
           content: '+';
           display: block;
           font-weight: 300;
           font-size: 1.4rem;
          }
        }
      }

      .action-container {
        display: flex;
        align-items: center;
        margin-left: auto;
      }

      .board-column-title {
        font-weight: 500;
        font-size: .9rem;
      }

      .task-count {
        background-color: #e3e8ee;
        padding: 3px 5px;
        font-weight: 500;
        margin: 0 10px;
        border-radius: 3px;
        font-size: .8rem;

        &.task-count-limit {
          background-color: $danger-color;
          color: white;
        }
      }

      .board-column-expanded {
        display: flex;
        align-items: center;
      }
    }

    th.board-column-header.board-column-header-collapsed,
    td.board-column-task-collapsed  {
      padding: 0px;
      width: 40px;
      min-width: 40px;

      .board-add-icon {
        display: none;

      }
    }

    tr {
      border-radius: 5px;

      &[id^="swimlane-"] {

        th.board-swimlane-header {
          top: 145px;
          position: sticky;
          z-index: 10;
        }

        ~ tr[class^="board-swimlane-columns-"] {

          th.board-column-header {
            top: 194px;
          }
        }
      }

      th {
        border-top-right-radius: 5px;
        border-top-left-radius: 5px;
        padding: 10px;
        border: 0 none;

        &.board-column-header {
          position: sticky;
          top: 145px;
          z-index: 10;
        }
      }

      td {
        border-bottom-right-radius: 5px;
        border-bottom-left-radius: 5px;
        padding: 0 10px 10px;
      }

      td {
        background-color: $default-bg-color-2;
        border-style: none;
      } 

      th:not(.board-swimlane-header) {
        background-color: $default-bg-color-3;
        border-style: none;
        border-bottom: 1px solid white;
      }

      .board-swimlane-header {
        background-color: $default-bg-color;

        a {
          padding-right: 10px;
        }
      }
    }

    &.first-swimlane-table {
      z-index: 10;
      margin-bottom: 0;
      position: sticky;
      top: 145px;

      &::before {
        content: '';
        height: 49px;
        background-color: $default-bg-color;
        position: fixed;
        left: 0;
        right: 0;
        top: 145px;
        z-index: -10;
      }

      ~ table {

        .board-column-header {
          top: 194px;
        }
      }
    }
    
    .board-task-list {
      margin-top: 10px;
    }

    .board-column-header-task-count {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #e3e8ee;
      padding: 3px;
      font-weight: 500;
      margin: 0 5px;
      border-radius: 3px;
      font-size: .8rem;

      &[class^="board-column-header-task-count swimlane-task-count-"] {
        display: inline-block;
        padding: 5px 8px;
      }
    }

    .board-column-collapsed {

      .btn-circle {
        margin-left: auto;
        margin-right: auto;
      }
    }

    .board-rotation-wrapper {
      display: flex;
      padding: 0;
      left: 50%;
      overflow: unset;
      min-height: 450px;

      .board-column-title.board-rotation {
        transform-origin: 0 50%;
        transform: rotate(90deg);
        height: 100%;
      }
    }
  }
}
