.board-task-list {

  .task-board {
    padding: 10px;
    margin-bottom: 10px;
    border-style: none;
    background-color: white;
    transition: box-shadow .3s;

    .task-board-header {
      height: 30px;
      padding-bottom: 10px;
      display: flex;
      align-items: center;

      .dropdown {

        .dropdown-menu strong {
          color: $default-title-color;

          i {
            color: #afafaf;
  
          }
        }

        &:hover .fa.fa-caret-down,
        &:hover .dropdown-menu strong {
          color: black;
        }
      }

      a.js-modal-large {
        margin-left: auto;
        background-color: $default-bg-color;
        text-align: center;
        width: 30px;
        height: 30px;
        font-size: 18px;
        color: $default-title-color;
        border-radius: 50%;
        transition: color .3s, background-color .3s;

        i {
          font-size: 14px;
          padding-top: 8px;
          padding-left: 2px;
        }

        &:hover,
        &:focus {
          color: black;
          background-color: #e3e8ee;
        }
      }
    }

    .task-board-title {
      margin-top: 0;
      margin-bottom: 0;
      padding-bottom: 10px;

    }

    .task-board-category-container {
      text-align: initial;
      margin-top: 0;
      margin-bottom: 0;
      padding-bottom: 10px;

      .task-board-category {
        border-style: none;
        padding: 2px 4px;
      }
    }

    .task-tags {
      padding-bottom: 0;
      border-top: 1px solid $default-bg-color-10;
      padding-top: 10px;

      li.task-tag {
        border: 0 none;
        padding: 2px 4px;
        margin: 0 8px 6px 0;
      }
    }

    .task-board-icons {
      margin-top: 0;
      text-align: left;
      padding-bottom: 5px;

      .avatar img {
        width: 33px;
      }

      .task-board-icons-row {
        margin-bottom: 5px;

        > span, div {
          margin-left: 0;
          padding-left: 6px;
          padding-right: 6px;

          &:not(:last-child) {
            border-right: 1px solid $default-bg-color-10;

          }

          &:first-child {
            padding-left: 0;
          }
        }

        .task-icon-age {
          span:first-child {
            margin-left: 0;
          }
        }
      }
    }

    &:hover,
    &:focus {
      box-shadow: 0 2px 1px #dfe1e2; // TODO VAR Env
    }

    a:hover,
    &:focus {
      text-decoration: none;

      &.js-modal-large {
        color: lighten(black, 30);
      }
    }

    .task-board-avatars {
      text-align: left;
      margin-right: -5px;
      margin-top: -4px;

      span {
        opacity: .8;
      }
    }
  }

  .draggable-placeholder {
    background-color: $default-bg-color-5;
    border-color: $default-bg-color-25;
  }
}
