@mixin button($bg-color, $text-color, $border-color, $bg-color-hover, $text-color-hover, $border-color-hover) {
  background-color: $bg-color;
  color: $text-color;
  border-color: $border-color;

  i,
  &:before {
    transition: color .3s;
  }

  &:hover,
  &:focus {
    background-color: $bg-color-hover;
    color: $text-color-hover;
    border-color: $border-color-hover;

    i,
    &:before {
      color: $text-color-hover;
    }
  }
}

.btn,
.form-actions > a {
  border-radius: 5px;
  padding: .375rem .75rem;
  border: 0.0625rem solid;
  display: inline-block;
  transition: color .6s, background-color .6s;
  cursor: pointer;
  font-weight: 300;
  line-height: 1.5;
  font-size: 1rem;
  width: auto;
  text-decoration: none;
  font-family: 'montserrat', sans-serif;
  @include button(white, #7f7f7f, #e2e2e2, #fafbfc, #7f7f7f, darken(#ececec, 10));

  &.btn-blue {
    @include button($btn-blue-color, white, $btn-blue-color, darken($btn-blue-color, 10), white, darken($btn-blue-color, 10));
  }

  &.btn-red {
    @include button($btn-red-color, white, $btn-red-color, darken($btn-red-color, 10), white, darken($btn-red-color, 10));
  }
}

