.btn-circle {
  border-radius: 50%;
  width: 38px;
  height: 38px;

  a:hover,
  a:focus {
    text-decoration: none;
    color: inherit;
  }

  &.btn-sm,
  &.btn-sm i,
  &.btn-sm i:before {
    width: 26px;
    height: 26px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.btn-primary {
  @include button($primary-color, white, $primary-color, darken($primary-color, 10), white, darken($primary-color, 10));
}

.btn-default {
  @include button(white, $default-color, white, lighten($primary-color, 30), $primary-color, lighten($primary-color, 30));
}