.select-dropdown-input-container {
  width: auto;
  border-color: $border-color;

  // TODO: remove !important
  input.select-dropdown-input {
    margin: 0 !important;
    padding: .375rem 2.2rem .375rem .75rem !important;
    border: 0 none !important;
  }

  .select-dropdown-chevron,
  .select-loading-icon {
    top: 10px;
    right: 10px;
  }
}

header {

  .select-dropdown-input-container {
    background-color: transparent;
    border: 0 none;

    input.select-dropdown-input {

      &:focus {
        box-shadow: 0 0 0 0.2rem rgba(255, 255, 255, 0.2) !important;
      }
    }
  }

}

#select-dropdown-menu {
  overflow: auto;

  .select-dropdown-menu-item {
    color: $default-title-color;

    &.active {
      background-color: $default-bg-color-2;
    }
  }
}