a {

  .fa {
    color: inherit;
    padding-right: 0;
    width: auto;
  }
}

.fa.fa-cog,
.fa.fa-th {
  color: $default-title-color;
  
  &:hover,
  &:focus {
    color: black;
  }
}

.fa-fa-caret-down {
  color: $default-title-color;
}

.fa.fa-arrows-alt {
  color: $default-title-color;
}

.fa.fa-fw.fa-trash-o {
  margin-right: 3px;
}

.tooltip .fa-info-circle {
  color: inherit;
}