.project-overview-columns {
  display: flex;
  justify-content: space-between;

  .project-overview-column {
    text-align: center;
    margin: 0;
    padding: 0;
    border: 0 none;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    small,
    strong {
      color: $default-title-color;
    }
  }
}